.inp{
  border: 1px solid rgba(0, 0, 0 ,0.1);
  padding: 12px 20px;
  width: 30vw;
  border-radius: 12px;
  font-size: 16px;
}
.btn{
  position: relative;
  bottom: 3px;
  right: 25px;
  border: none;
  background-color: #fff;
  font-size: large;
  font-weight: bold;
}
@media only screen and (max-width: 780px) {
  .inp {
    width: 40vw;
    margin: 10px 20px; 
  }
}
@media only screen and (max-width: 480px) {
  .inp {
    width: 70vw;
  }
}